
 *{
     margin:0;
     padding: 0;
 }
*,*::before,*::after{
    box-sizing: border-box;
}
#div1{
    position: fixed;
    top: 20px;
    right: 100px;
}
#div1::before{
    position: absolute;
    content:'';
    display: block;
    border-radius: 50%;
}
#div1::after{
    position: absolute;
    content:'';
    display: block;
    border-radius: 50%;
}

@media(max-width:500px) {
    #html{
        height:50vh;
        overflow: auto;
    }
    #div1{
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);

    }
    #div1Wrapper{
        height:50vh;
        border:1px solid red;
        position: relative;
    }
    @keyframes spin{
        0%{
            transform:translate(-50%,-50%) rotate(0);
        }
        50%{
            transform: translate(-50%,-50%) rotate(180deg);
        }
        100%{
            transform: translate(-50%,-50%) rotate(360deg);            }
    }

}
#html{
    word-break:break-all;
}
